/** FONTS **/
/** COLORS **/
html {
  font-size: 100% !important;
}

body {
  line-height: 1.96rem !important;
  font-size: 1.4rem !important;
  font-weight: 400 !important;
}

.content-margin {
  margin-left: 120px !important;
  margin-right: 120px !important;
}

/*
.home .fusion-builder-row .fusion-builder-row {
  margin-left: initial !important;
  margin-right: initial !important;
}
*/
.post-content h1,
.title h1,
h1 {
  font-size: 2.6rem !important;
}

h1.entry-title,
h1.fusion-post-title {
  font-family: "zenon";
}

.fusion-page-title-bar .fusion-page-title-wrapper {
  align-items: end;
}
.fusion-page-title-bar .fusion-page-title-wrapper h1 {
  margin-bottom: 16px;
}

#main h2,
.post-content h2,
h2 {
  font-size: 1.9rem;
  margin-bottom: 30px;
}

a {
  text-decoration: underline !important;
}

a.fusion-button {
  text-decoration: none !important;
}

#main .post-content p {
  margin-bottom: 30px;
}

/** BANNER **/
.fusion-header-v4 .factfinder-header.fusion-header {
  padding-top: 20px;
  padding-bottom: 10px;
}

/** MAIN MENU **/
.fusion-secondary-main-menu {
  background-color: #5EAAD5 !important;
  margin-bottom: 4px;
}

.fusion-main-menu a {
  text-decoration: none !important;
}

.fusion-header-layout-v4.avada-menu-highlight-style-bottombar.fusion-top-header:not(.fusion-header-layout-v6) .fusion-main-menu > ul > li > .fusion-bottombar-highlight::after {
  border-color: #FF952A;
}

body.fusion-header-layout-v4:not(.fusion-header-layout-v6).fusion-top-header .fusion-main-menu > ul > li:not(.fusion-menu-item-button) > a.fusion-bottombar-highlight:not(.fusion-main-menu-icon)::after {
  top: 104%;
}

/** HOMEPAGE **/
.home #main {
  padding-top: 0;
}
.home #main .post-content p {
  margin-bottom: 20px;
}
.home .post-content h2 {
  color: #406253;
}
.home h1 p {
  margin-bottom: 0 !important;
}
.home h2.title-heading-left {
  margin-left: -20px !important;
}

.reduced-width,
.fusion-body .fusion-builder-column-1.homepage-slidertext-container,
.fusion-body .fusion_builder_column.homepage-narrow-text-container {
  width: 750px !important;
  margin-left: auto;
  margin-right: auto;
}

.fusion-body .fusion-builder-column-1.homepage-slidertext-container {
  left: 0;
  right: 0;
}

.homepage-container-margin {
  padding-top: 50px !important;
  padding-bottom: 60px !important;
}

.homepage-container-margin.search-factfinder-bottom-margin {
  padding-bottom: 20px !important;
}

.homepage-factfinder-topics.fusion-content-boxes {
  margin-top: 30px;
}
.homepage-factfinder-topics.fusion-content-boxes .content-box-column {
  padding-left: 30px !important;
  padding-right: 30px !important;
}
.homepage-factfinder-topics.fusion-content-boxes h3 {
  border-top: #EE750C 2px solid;
  padding-top: 10px !important;
  font-family: "mr-eaves-xl-modern-narrow";
  font-size: 1.7rem !important;
}

.homepage-factfinder-topics.fusion-content-boxes {
  margin-bottom: 0;
}

.homepage-go-to-factfinder-button {
  margin-top: 30px;
  margin-bottom: 60px;
}

.misinformation-content {
  align-self: center;
}

.fusion-body .fusion_builder_column.misinformation-graphic {
  margin-bottom: 0;
}

/** FACT PAGES **/
.claim-key-column {
  align-self: center !important;
}

.claim-key-content {
  display: flex;
  justify-content: flex-end !important;
}
.claim-key-content p {
  float: left;
  margin-bottom: 0 !important;
  line-height: 1;
}
.claim-key-content img {
  margin-left: 20px;
}

.fusion-body .fusion-button-default-size.claim-key-button {
  padding-left: 10px;
  padding-right: 10px;
  line-height: 0.8;
}

.claim-key-button span {
  font-size: 1.5rem;
}

#main .fusion-content-boxes.factfinder-entry-claim .fusion-column {
  margin-bottom: 30px !important;
}
#main .fusion-content-boxes.factfinder-entry-claim .heading-with-icon {
  margin-top: 0 !important;
}
#main .fusion-content-boxes.factfinder-entry-claim .content-box-heading {
  font-family: "mr-eaves-xl-modern" !important;
  font-weight: 500 !important;
  font-size: 1.6rem !important;
  color: #5EAAD5 !important;
}
#main .fusion-content-boxes.factfinder-entry-claim .content-box-heading::before {
  content: "CLAIM: ";
  color: #045593;
  font-weight: 800;
}

.post .fusion-content-boxes.factfinder-entry-claim .heading-with-icon .image {
  margin-bottom: 0;
}

#main .factfinder-entry-text h2,
#main .factfinder-list .fusion-post-content-container h2,
.post-content .factfinder-entry-text h2,
.factfinder-entry-text h2 {
  font-family: "mr-eaves-xl-modern" !important;
  font-weight: 800 !important;
  color: #045593 !important;
  border-bottom: #5791C2 1px solid;
}

#main .factfinder-entry-text h2,
.post-content .factfinder-entry-text h2,
.factfinder-entry-text h2 {
  margin-bottom: 20px;
  font-size: 1.6rem !important;
}

#main .factfinder-list .fusion-post-content-container h2:first-of-type {
  padding: 16px 10px 16px 50px;
  border-bottom: none;
  font-weight: 500 !important;
  color: #5EAAD5 !important;
}
#main [class*=category-] .fusion-text:first-of-type h2 {
  padding: 20px 10px 20px 60px;
}

[class*=category-] .fusion-text:first-of-type h2,
.factfinder-list .fusion-post-content-container h2:first-of-type {
  font-family: "mr-eaves-xl-modern" !important;
  font-weight: 500 !important;
  font-size: 1.6rem !important;
  color: #5EAAD5 !important;
  background-position: 0 50%;
  background-repeat: no-repeat;
  background-size: 41px 41px;
}

.factfinder-list .fusion-post-content-container h2:first-of-type {
  background-size: 41px 41px;
}

[class*=category-] .fusion-text:first-of-type h2 {
  background-size: 48px 48px;
}

[class*=category-] .fusion-text:first-of-type h2::before,
.factfinder-list .fusion-post-content-container h2:first-of-type::before {
  content: "CLAIM: ";
  color: #045593;
  font-weight: 800;
}

[class*=category-].tag-true .fusion-text:first-of-type h2,
.factfinder-list .tag-true .fusion-post-content-container h2:first-of-type {
  background-image: url("./images/true.svg");
}

[class*=category-].tag-false .fusion-text:first-of-type h2,
.factfinder-list .tag-false .fusion-post-content-container h2:first-of-type {
  background-image: url("./images/false.svg");
}

[class*=category-].tag-unsure .fusion-text:first-of-type h2,
.factfinder-list .tag-unsure .fusion-post-content-container h2:first-of-type {
  background-image: url("./images/unsure.svg");
}

/** NORMAL PAGES **/
.fusion-content-boxes.factfinder-callout-box {
  margin-bottom: 0;
}
.fusion-content-boxes.factfinder-callout-box .content-wrapper-background {
  padding: 0;
}
.fusion-content-boxes.factfinder-callout-box .content-container,
.fusion-content-boxes.factfinder-callout-box .heading .content-box-heading {
  padding-left: 26px;
}
.fusion-content-boxes.factfinder-callout-box .heading .content-box-heading {
  background: #045593;
  font-family: "mr-eaves-xl-modern";
  font-weight: 700;
  font-size: 1.8rem !important;
  padding-top: 13px;
  padding-bottom: 13px;
  padding-right: 26px;
}
.fusion-content-boxes.factfinder-callout-box .content-container {
  padding-top: 30px;
  padding-bottom: 30px;
  padding-right: 60px;
}
.fusion-content-boxes.factfinder-callout-box .content-container p {
  margin-bottom: 40px !important;
}

/** BUTTONS **/
.fusion-button {
  font-size: 1.7rem !important;
}

/** UTILITIES **/
.center-content {
  text-align: center;
}

.indent-more {
  margin-left: 60px;
}

.content-spacing {
