  margin-bottom: 40px;
}

/** FOOTER **/
.footer-menu-and-legal {
  text-align: center;
  color: #fff;
}

.footer-menu-and-legal a {
  text-decoration: underline;
  color: #fff;
  font-weight: 400;
}

.footer-menu-and-legal a:hover {
  color: #ccc;
}

.footer-menu-and-legal .footer-menu {
  display: inline-block;
}

.footer-menu-and-legal .footer-menu li {
  float: left;
  padding: 0 10px;
  border-right: #fff 1px solid;
}

.footer-menu-and-legal .footer-menu li:last-child {
  border-right: none;
  padding-right: 0;
}

footer .footer-menu-and-legal .footer-menu a {
  font-size: 0.9em;
}

footer .footer-menu-and-legal .footer-legal {
  font-size: 0.8em;
}

footer .footer-menu-and-legal .footer-legal a {
  font-size: 1em;
}